import React from 'react';
import Navbar from '../component/Navbar'; // Default import
import Hero from './Hero'; // Default import
import About from './About'; // Default import
import Skills from './Skills'; // Default import
import Projects from './Projects'; // Default import
import Contact from './Contact'; // Default import
import Footer from '../component/Footer'; // Default import
import HeroMetrics from '../component/HeroMetrics';

const Home = () => {
  return (
    <div className="bg-slate-900">
      <Navbar />
      <main>
        <Hero />
        <About />
        <Skills />
        {/* <HeroMetrics /> */}
        <Projects />
        <Contact /> 
      </main>
      <Footer />
    </div>
  );
};

export default Home;
