import React from 'react';
import './Skills.css';
import { Typography } from 'antd';

function Skills() {
  
const { Title, Paragraph } = Typography;

  return (
    <section id="skills">
        <Title
          level={4}
          style={{
            color: '#000000',
            textAlign: 'left', // Left aligned
            fontFamily: 'Poppins, "Segoe UI", Roboto, sans-serif'
          }}
        >Key Contributions
        </Title>
      <div className="mt-8">
        <ul className="list-disc pl-6 text-lg text-slate-300">
          <li>Delivered 40+ production-grade sprints with zero spillovers, ensuring stable releases across three enterprise platforms</li>
          <li>Resolved 30+ high-impact full-stack bugs, enhancing system reliability and end-user experience</li>
          <li>Integrated 12+ secure SSO flows using OAuth 2.0 and SAML across single- and multi-tenant applications</li>
          <li>Optimized 40+ SQL queries using PostgreSQL and Oracle tuning, improving execution time by over 35%</li>
          <li>Built and deployed bulk automation modules for counselor and sponsor workflows, accelerating operational efficiency</li>
        </ul>
      </div>
    </section>
  );
}

