import React from 'react';
import { Card, Typography, Row, Col } from 'antd';
import './Projects.css';

const { Title, Paragraph } = Typography;

const projectsData = [
  {
    title: 'Enterprise Application Modernization – 1to1Help',
    summary: 'Modernized legacy enterprise systems into scalable web and mobile apps.',
    details: 'Built Spring Boot REST APIs and integrated ReactJS frontends, enhancing UX and performance while securing flows using SAML SSO and enabling real-time interactions with WebSockets.',
    techStack: ['ReactJS', 'Spring Boot', 'Java', 'PostgreSQL', 'WebSockets', 'SAML SSO']
  },
  {
    title: 'LifeSphere Medical Information Platform (LSMI)',
    summary: 'Enhanced core drug-lifecycle platform by stabilizing and migrating backend systems.',
    details: 'Migrated database from Oracle 18c to PostgreSQL, implemented scheduler-based automation for workflows, and maintained system integrity across multiple tenants.',
    techStack: ['Java', 'JSF', 'PrimeFaces', 'Oracle 18c', 'PostgreSQL', 'Scheduler Services']
  },
  {
    title: 'Counsellor Talks CMS & Chat Automation',
    summary: 'Built key CMS features and real-time chat for counseling service platform.',
    details: 'Developed bulk activity upload and report assignment modules using Spring Boot and React, while integrating WebSocket-based chat and secure authentication with SAML SSO.',
    techStack: ['Java', 'Spring Boot', 'React', 'WebSockets', 'SAML SSO', 'SQL']
  },
  {
    title: 'Microservice Simulation Platform',
    summary: 'Developed microservice mocking system to enable parallel frontend/backend workflows.',
    details: 'Used Wiremock for API simulation, integrated OKTA for secure access control, and managed DB changes using Flyway across multiple services.',
    techStack: ['Java', 'Wiremock', 'Spring Boot', 'OKTA', 'Flyway', 'Microservices']
  },
];

const Projects = () => (
  <div className="projects-section" id="projectId">
    <Row justify="center" style={{ padding: '0 50px' }}>
      <Col xs={24}>
        <Title level={2} className="projects-title">
          Key Projects
        </Title>
        <Row gutter={[32, 32]} justify="center">
          {projectsData.map((project, index) => (
            <Col xs={24} sm={12} md={12} lg={12} key={index}>
              <Card className="project-card" bordered={false}>
                <Title level={4}>{project.title}</Title>
                <Paragraph style={{ fontWeight: 500, marginBottom: '0.8rem' }}>{project.summary}</Paragraph>
                <Paragraph>{project.details}</Paragraph>
                <Paragraph className="project-tech-stack">
                  <strong>Tech Stack:</strong> {project.techStack.join(', ')}
                </Paragraph>
              </Card>
            </Col>
          ))}
        </Row>
      </Col>
    </Row>
  </div>
);

export default Projects;
