import React from 'react';
import { Row, Col, Card, Typography } from 'antd';
import { 
  DatabaseOutlined, BugOutlined, TrophyOutlined, CheckCircleOutlined, 
  SafetyCertificateOutlined, ThunderboltOutlined, KeyOutlined,
  RobotOutlined, CodeOutlined, ApiOutlined, LayoutOutlined
} from '@ant-design/icons';
import './HeroMetrics.css';

const { Title, Text } = Typography;

// Standard metrics
const standardMetrics = [
  { icon: <DatabaseOutlined />, title: '54+', description: 'Queries — Optimized for DB performance' },
  { icon: <TrophyOutlined />, title: '48+', description: 'Sprints — Delivered without defect spillovers' },
  { icon: <BugOutlined />, title: '32+', description: 'Bugs — Fixed across full stack' },
  { icon: <CheckCircleOutlined />, title: '7+', description: 'Critical Prod Issues Resolved (Hot fix)' },
  { icon: <SafetyCertificateOutlined />, title: '4+', description: 'Professional Certifications' },
  { icon: <ThunderboltOutlined />, title: '100%', description: 'Sprint Delivery Success Rate' },
  { icon: <KeyOutlined />, title: '12+', description: 'SSO Integrations (OAuth, SAML, single and multi-tenant)' },
];

// Certification metrics with outline icons
const certificationMetrics = [
  { 
    icon: <RobotOutlined />, 
    title: 'Google AI Essentials (2025)',
    description: 'via Coursera' 
  },
  { 
    icon: <CodeOutlined />, 
    title: 'Spring with Spring Boot (2022)',
    description: 'via LinkedIn Learning' 
  },
  { 
    icon: <ApiOutlined />, 
    title: 'Java Persistence API (2022)',
    description: 'via LinkedIn Learning' 
  },
  { 
    icon: <LayoutOutlined />, 
    title: 'React for Web Designers (2022)',
    description: 'via LinkedIn Learning' 
  },
];

const HeroMetrics = () => (
  <section className="hero-metrics">
    <div className="metrics-container">
      <Title level={2} className="metrics-title">Metrics</Title>
      <Row gutter={[16, 16]} justify="center">
        {standardMetrics.map((metric, index) => (
          <Col xs={12} sm={8} md={6} lg={4} key={index}>
            <Card className="metric-card" hoverable>
              <div className="metric-icon">{metric.icon}</div>
              <Title level={4} className="metric-title">{metric.title}</Title>
              <Text className="metric-description">{metric.description}</Text>
            </Card>
          </Col>
        ))}
        
        {certificationMetrics.map((cert, index) => (
          <Col xs={12} sm={8} md={6} lg={4} key={`cert-${index}`}>
            <Card className="metric-card" hoverable>
              <div className="metric-icon">{cert.icon}</div>
              <Title level={4} className="metric-title">{cert.title}</Title>
              {cert.description && (
                <Text className="metric-description">{cert.description}</Text>
              )}
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  </section>
);

export default HeroMetrics;
