import React, { useState, useEffect } from 'react';
import { LinkedinOutlined, MailOutlined, PhoneOutlined, DownloadOutlined } from '@ant-design/icons';
import { Button, Typography, Row, Col } from 'antd';

import './Hero.css'; 
import SplitText from "../component/animations/SplitText.js";
import BlurText from "../component/animations/BlurText .js";
import DecryptedText  from '../component/animations/DecryptedText .js';

const Hero = () => {
  const [clickedItem, setClickedItem] = useState(null);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const handleCvButtonClick = () => {
    setClickedItem('cv');
    // Reset the clicked state after animation completes
    setTimeout(() => {
      setClickedItem(null);
    }, 500);
  };

  return (
    <div className="hero-container">
      <Row justify="center" align="middle" style={{ minHeight: '100vh' }}>
        <Col xs={24} md={16}>
          <div className="hero-content">
            <SplitText
              text="Gunapal P"
              className="hero-name"
              delay={200}
              duration={0.8}
              ease="power3.out"
              splitType="chars"
              from={{ opacity: 0, y: 40 }}
              to={{ opacity: 1, y: 0 }}
              threshold={0.1}
              rootMargin="-100px"
            />
            <BlurText
              text="Full Stack Java Developer"
              delay={150}
              animateBy="words"
              direction="top"
              className="hero-title"
            />
            {isMobile ? (
              <div className="hero-description">
                5+ years of experience building scalable enterprise applications with Java, Spring Boot, and ReactJS.
              </div>
            ) : (
              <DecryptedText
                text="5+ years of experience building scalable enterprise applications with Java, Spring Boot, and ReactJS."
                animateOn="view"
                revealDirection="center"
                className="hero-description"
                speed={300}
                maxIterations={8}
              />
            )}
            <div className="hero-social-links">
              <Button 
                type="link" 
                href="tel:+918050735082" 
                icon={<PhoneOutlined style={{ fontSize: '24px', transform: 'scaleX(-1)' }} />} 
                className="hero-social-btn"
              />
              <Button 
                type="link" 
                href="https://linkedin.com/in/gunapal-p" 
                icon={<LinkedinOutlined style={{ fontSize: '24px' }} />} 
                className="hero-social-btn"
              />
              <Button 
                type="link" 
                href="mailto:gunapalpgp@gmail.com" 
                icon={<MailOutlined style={{ fontSize: '24px' }} />} 
                className="hero-social-btn"
              />
            </div>
            <Button 
              type="default" 
              href="/gunapal.github.io/Gunapal.pdf"
              target="_blank" 
              rel="noopener noreferrer" 
              download
              icon={<DownloadOutlined />}
              onClick={handleCvButtonClick}
              className={`cv-button ${clickedItem === 'cv' ? 'clicked' : ''}`}
            >
              Download CV
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Hero;
