import React from 'react';
import { Github, Linkedin, Mail, Phone, MapPin, Send } from 'lucide-react';
import { Typography, Space } from 'antd';
import './Contact.css';

const { Title, Text } = Typography;

const Contact = () => (
  <section id="contact" className="contact-section">
    <div className="contact-container">
      <Title level={2} className="contact-title">Get In Touch</Title>
      <Text className="contact-description">
        I'm currently open to new opportunities and collaborations. Whether you have a question or just want to say hi, my inbox is always open. I'll get back to you as soon as possible!
      </Text>
      
      <a href="mailto:gunapalpgp@gmail.com" className="email-invitation">
        <div className="email-card">
          <div className="email-icon">
            <Send size={24} />
          </div>
          <span className="email-text">Drop me a mail</span>
        </div>
      </a>
      
      <div className="contact-info-container">
        <div className="contact-icons">
          <a href="https://github.com/gunapal-p" target="_blank" rel="noopener noreferrer"><Github size={32} /></a>
          <a href="https://linkedin.com/in/gunapal-p" target="_blank" rel="noopener noreferrer"><Linkedin size={32} /></a>
          <a href="mailto:gunapalpgp@gmail.com"><Mail size={32} /></a>
        </div>
        
        <div className="contact-details">
          <div className="contact-detail-item">
            <Phone size={24} />
            <Text>+91 8050735082</Text>
          </div>
          <div className="contact-detail-item">
            <MapPin size={24} />
            <Text>Banglore India</Text>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Contact;
