import React, { useState, useEffect } from 'react';
import { Button, Drawer, Menu } from 'antd';
import { MenuOutlined, HomeOutlined, DownloadOutlined } from '@ant-design/icons';
import './Navbar.css';

const Navbar = () => {
  const [visible, setVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const [clickedItem, setClickedItem] = useState(null);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Handle click effect that shows temporarily
  const handleClickEffect = (itemKey) => {
    setClickedItem(itemKey);
    setTimeout(() => {
      setClickedItem(null);
    }, 800); // Effect duration in milliseconds
  };

  const navigateToHome = () => {
    handleClickEffect('logo');
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  
  const navigateToSection = (sectionId) => (e) => {
    e.preventDefault();
    handleClickEffect(sectionId);
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const navItems = [
    { 
      key: 'about', 
      label: <a href="#about" onClick={navigateToSection('about')} className={clickedItem === 'about' ? 'clicked' : ''}>About</a>,
      className: clickedItem === 'about' ? 'clicked' : ''
    },
    { 
      key: 'skills', 
      label: <a href="#skills" onClick={navigateToSection('skills')} className={clickedItem === 'skills' ? 'clicked' : ''}>Impact</a>,
      className: clickedItem === 'skills' ? 'clicked' : ''
    },
    { 
      key: 'projects', 
      label: <a href="#projects" onClick={navigateToSection('projectId')} className={clickedItem === 'projectId' ? 'clicked' : ''}>Projects</a>,
      className: clickedItem === 'projectId' ? 'clicked' : ''
    },
    { 
      key: 'contact', 
      label: <a href="#contact" onClick={navigateToSection('contact')} className={clickedItem === 'contact' ? 'clicked' : ''}>Contact</a>,
      className: clickedItem === 'contact' ? 'clicked' : ''
    },
  ];

  const handleCvButtonClick = () => {
    handleClickEffect('cv');
  };

  return (
    <nav className="custom-navbar glass-effect">
      <div className="navbar-content">
        <Button 
          onClick={navigateToHome}
          className={`logo-button ${clickedItem === 'logo' ? 'clicked' : ''}`}
          type="text"
        >
          GP
        </Button>

        {isMobile ? (
          <>
            <Button
              icon={<MenuOutlined />}
              onClick={() => setVisible(true)}
              className="menu-button"
            />
            <Drawer
              title="Menu"
              placement="right"
              onClose={() => setVisible(false)}
              open={visible}
            >
              <Menu
                mode="vertical"
                items={[
                  ...navItems,
                  {
                    key: 'cv',
                    label: (
                      <Button 
                        type="default" 
                        href="/gunapal.github.io/Gunapal.pdf"
                        target="_blank" 
                        rel="noopener noreferrer"
                        download
                        icon={<DownloadOutlined />}
                        block
                        onClick={handleCvButtonClick}
                        className={clickedItem === 'cv' ? 'clicked' : ''}
                      >
                        Download CV
                      </Button>
                    ),
                  },
                ]}
              />
            </Drawer>
          </>
        ) : (
          <div className="navbar-desktop-links">
            <Menu
              mode="horizontal"
              theme="dark"
              items={navItems}
              className="menu"
              overflowedIndicator={null} // Prevents the ellipsis from showing
              disabledOverflow={true} // Ensures all items are always visible
            />
            <Button
              type="default"
              href="/gunapal.github.io/Gunapal.pdf"
              target="_blank" 
              rel="noopener noreferrer"
              download
              icon={<DownloadOutlined />}
              onClick={handleCvButtonClick}
              className={`cv-button ${clickedItem === 'cv' ? 'clicked' : ''}`}
            >
              Download CV
            </Button>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
